import React, { Component } from 'react';
import { ReactiveBase, ReactiveList,DataSearch, DataController} from '@appbaseio/reactivesearch';
import './App.css';
import planecrop from './img/planecrop.png';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      lat: 0,
      lng: 0,
      loading:true,
    };
  }
componentDidMount() {
    window.navigator.geolocation.getCurrentPosition(position => {
      this.setState({ lat: position.coords.latitude, lng: position.coords.longitude, loading:false });
    }, err => console.log(err)
    );
  }	


	render() {
		console.log(this.state.lat);
		if(this.state.loading){
			return 'loading... ensure geo location is allowed';
		}
		return (
		    <div className="container" style={{"paddingLeft":"10%", "paddingRight":"10%"}}>
		        <div className="row" style={{"marginBottom":"-30px"}}>
		          <div className="col align-self-center">
		            <h1>Get Spirited Away</h1>
		          </div>
		        </div>
		        <div className="row" style={{"paddingBottom":"20px"}}>
		          <div className="col align-self-center" >
		           <img className="img-plane" src={planecrop} alt="plane"></img>
		          </div>
	        	</div>
				<ReactiveBase
					app="getspiritedaway_ver14"
					credentials="1S80bZFpv:287d78d4-5267-4b72-8205-93246dedb7f7">
					<DataController
						componentId="geoloc"
						dataField='loc'
						value={this.state.lng}
						customQuery={(value, props) => ({ geo_distance: { distance:'100mi','loc':{'lon':this.state.lng,'lat':this.state.lat}}})}
					/>
					<DataSearch
					  className="btrow"
					  style={{"paddingLeft":"10%", "paddingRight":"10%","marginBottom":"20"}}
					  autosuggest={false}
					  componentId="search"
					  dataField={[
					    'description',
					    'description.autosuggest',
					    'description.keyword'
					  ]}
					  fieldWeights={[
					    1,
					    1,
					    1
					  ]}
					  fuzziness={0}
					  highlightField={[
					    'description'
					  ]}
					  placeholder="put in your ideal vacation"
					/>
					<div className="col">
						<ReactiveList
							componentId="result"
							dataField="miniPrice"
							sortBy="asc"
							pagination={true}
							size={10}
							react={{
						    	and: [
						    	'search',
						        'geoloc'
						    	]
							}}
							onData={this.vacaresults}
						
						/>	
					</div>
				</ReactiveBase>
			</div>
		);
	}


	vacaresults(res){
		var regExp = /\(([^)]+)\)/;
		var depprice=parseFloat(res.miniPrice).toFixed(0);
		var retprice=parseFloat(res.returnPrice).toFixed(0);
		var price=parseInt(depprice)+parseInt(retprice);
		var snip=res.description.substring(0,200);
		var dest=res.dest.split(',')[0];
		var depdate=res.Date;
		var retdate=res.returnDate;
		const toFlight= regExp.exec(res.depart)[1] +"." + regExp.exec(res.dest)[1]+"."+ depdate;
		const retFlight= regExp.exec(res.dest)[1]+"." + regExp.exec(res.depart)[1] +"."+ retdate;
		const gFlights="https://www.google.com/flights?hl=en#flt="+
			toFlight+"*"+retFlight+";c:USD;e:1;sd:1;t:f";
		dest=dest.split('/')[0];
		const lonelyplanet="https://www.lonelyplanet.com/search?q="+dest
		return(
				<div className="flex result-content">
				
					<img className="url-image" src={res.url} alt="GSA"/>
				
					<div className="details result-header">
				
						<div className="info1 city-header">
				
							<a className="city-header" href={gFlights} 
							target="_blank" rel="noopener noreferrer" >{dest}</a>
				
							<div className="info1 date-header">{res.departmonth}
							
							</div>
				
						</div>
				
						<div className="info2 price-header">
				
							<a style={{display: "table-cell"}} href={gFlights} 
							target="_blank" rel="noopener noreferrer" >${price}</a>
						
						</div>
				
						<div className="result-footer">
				
							<a >{snip}
								<a style={{color:"#071062"}} href={lonelyplanet} 
								target="_blank" rel="noopener noreferrer">
									...Click to read more on Lonely Planet
								</a>
							</a>
						</div>
				
					</div>
				
				</div>
			);
	}

}

  

export default App;